#overlay {
  /*　画面全体を覆う設定　*/
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);

  /*　画面の中央に要素を表示させる設定　*/
  display: flex;
  align-items: center;
  justify-content: center;
}

#content {
  z-index: 2;
  width: 95%;
  height: 95%;
  padding: 1em;
  background: #fff;
  overflow-y: scroll;
}

#fulllist {
  background-color: white;
  overflow-y: scroll;
  font-size: 150%;
  font-family: monospace;
  display: inline-table;
}
