.overlay {
  /*　画面全体を覆う設定　*/
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);

  /*　画面の中央に要素を表示させる設定　*/
  display: flex;
  align-items: center;
  justify-content: center;
}
.content {
  position: absolute;
  z-index: 10;
  width: 100%;
  height: 100%;
  background: #fff;
}
.toggle-button-001 {
  display: inline-block;
  position: relative;
  width: 30px;
  height: 15px;
  border-radius: 50px;
  background-color: #dddddd;
  cursor: pointer;
  transition: background-color 0.4s;
}

.toggle-button-001:has(:checked) {
  background-color: #4bd865;
}

.toggle-button-001::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 15px;
  border-radius: 50%;
  box-shadow: 0 0 5px rgb(0 0 0 / 20%);
  background-color: #fff;
  content: "";
  transition: left 0.4s;
}

.toggle-button-001:has(:checked)::after {
  left: 15px;
}

.toggle-button-001 input {
  display: none;
}
