.clock {
  position: relative;
  height: 320px;
  margin: 20px auto;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: lightyellow;
  user-select: none;
  pointer-events: none;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  overflow: hidden;
}

.seconds {
  height: 320px;
  justify-content: center;
  position: absolute;
  user-select: none;
  pointer-events: none;
}

.inner {
  position: absolute;
  transform: translate(2px, 2px);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  transform-style: preserve-3d; /* 子要素の短針と長針をZ軸方向にマイナス移動させるため、3D空間に */
}

.time {
  font-family: monospace, serif;
  text-align: center;
}
