@import url("https://cdn.jsdelivr.net/npm/bootstrap@5.0.2/dist/css/bootstrap.min.css");

.card {
  position: relative;
}

.card__link::after {
  position: absolute;
  pointer-events: auto;
  content: "";
  background-color: transparent;
}

.Super {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: lightyellow;
}

.Lower {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: lightyellow;
}

.List {
  background-color: white;
}

.Reset {
  background-color: limegreen;
}

.ol {
  cursor: pointer;
  position: sticky;
  font-family: monospace;
  font-size: x-large;
  list-style-position: inside;
  padding-left: 0;
}

.time {
  font-size: 180%;
}

.pop {
  display: inline-block;
  list-style: none;
  width: 50%;
}

.lappop {
  border: 1px;
  display: inline-block;
  width: 50%;
}

.li {
  overflow-y: scroll;
}
